@import "ScssHelpers/helpers-all.scss";

.video_and_picture {
	position: relative;
}

.video_and_picture__img {
	position: relative;
	//padding-top: 51.6%;
	display: block;
	width: 100%;
	height: 100%;

	&:not(:last-child) {
		margin-bottom: 1.8rem;
	}

	@include bp($point_4, min) {
		padding-top: 41.2%;
	}

	@include bp($point_4 - 1) {
		padding-top: 57%;
	}
}

.video_and_picture__img_in {
	width: 100%;
	height: 100%;
	display: block;
	object-fit: cover;

	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

.video_and_picture__title {
	@include h2;

	letter-spacing: -.053em;

	&:not(:last-child) {
		margin-bottom: 1.6rem
	}
}

.video_and_picture__text {
	@include text;

	width: 98%;
	letter-spacing: -.025em;
	line-height: 1.48;

	&:not(:last-child) {
		margin-bottom: 4.1rem
	}
}

.video_and_picture__descrip {
	font-size: 1.4rem;
	letter-spacing: -.009em;
}

.video_and_picture__icon {
	display: block;

	&:not(.full_width_video &) {
		@include bp($point_2, min) {
			width: 12rem;
			height: 12rem;
		}
	}

	.full_width_video & {
		@include bp($point_2, min) {
			width: 16rem;
			height: 16rem;
		}
	}

	@include bp($point_3) {
		width: 10.4rem;
		height: 10.4rem;
	}
}

.video_and_picture__link {
	position: absolute;
	top: 50%;
	left: 50%;

	color: $white;

	&:not(.full_width_video &) {
		@include bp($point_2, min) {
			margin-top: -8rem;
			margin-left: -6rem;
		}
	}

	.full_width_video & {
		@include bp($point_2, min) {
			margin-top: -10rem;
			margin-left: -8rem;
		}
	}

	@include bp($point_3) {
		margin-top: -7.6rem;
		margin-left: -5.2rem;
	}
}
