@import "ScssHelpers/helpers-all.scss";

.full_width_video {
	// padding-top: 8.8rem;
	// padding-bottom: 14.4rem;
}

.full_width_video_in {
}

.full_width_video__title {
	@include h2;

	letter-spacing: -0.017em;

	&:not(:last-child) {
		margin-bottom: 1.5rem;
	}
}

.full_width_video__text {
	@include text;

	width: 95%;

	line-height: 1.5;

	&:not(:last-child) {
		margin-bottom: 4.1rem;
	}
}

.full_width_video__link {
}

.full_width_video__img_wrap {
	position: relative;
}
.full_width_video__img_in {
	position: relative;

	display: block;
	width: 100%;
	height: 100%;
	min-height: 50rem;
	padding-bottom: 41.2%;
}

.full_width_video__img {
	@include stretch;
	@include cover_img;
}

.full_width_video__link {
	position: absolute;
	top: 50%;
	left: 50%;

	width: 16rem;
	height: 16rem;

	color: $white;

	transform: translate(-50%, -50%);
}
