@import "ScssHelpers/helpers-all.scss";

.content_banner_video {
	// padding-bottom: 0;
	// padding-top: 8rem;
}

.content_banner_video__wrap {
	position: relative;

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center);
	}
}

.content_banner_video__decor {
	@include bp($point_2, min) {
		padding-top: 10.4rem;
	}
	@include bp($point_3) {
		padding-top: 5.4rem;
	}
}

.content_banner_video__media {
	position: relative;

	height: 0;

	@include bp($point_1, min) {
		margin-left: -10.8rem;
	}

	@include bp($point_2, min) {
		flex-shrink: 0;
		&:not(:last-child) {
			margin-right: 9.2rem;
		}
	}
	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}

	@include bp($point_3, $point_4) {
		margin-left: auto;
		margin-right: auto;
	}

	@include bp($point_4, min) {
		width: 52.2%;
		padding-bottom: 42.5%;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		padding-bottom: 81%;
	}
}

.content_banner_video__img_in {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;
}

.content_banner_video__img {
	@include cover_img;
}

.content_banner_video__trigger {
	@include transition_all;

	position: absolute;
	left: 50%;
	top: 50%;

	color: $white;

	@include bp($point_2, min) {
		margin: -6rem 0 0 -6rem;
		width: 12rem;
		height: 12rem;
	}
	
	@include bp($point_3) {
		margin: -4.8rem 0 0 -4.8rem;
		width: 9.6rem;
		height: 9.6rem;
	}

	&:hover {
		transform: scale(1.1);
		color: $gold_2;
	}
}

.content_banner_video__content {
	@include bp($point_2, min) {
		margin-top: -2.4rem;
	}
}

.content_banner_video__title {
	@include h2;

	@include bp($point_4, min) {
		letter-spacing: -0.016em;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	@include bp($point_4 - 1) {
		letter-spacing: -0.024em;

		&:not(:last-child) {
			margin-bottom: 0.6rem;
		}
	}
}

.content_banner_video__text {
	@include text;

	p {
		&:not(:last-child) {
			margin-bottom: 0.8rem;
		}
	}

	@include bp($point_4, min) {
		line-height: 1.5;

		&:not(:last-child) {
			margin-bottom: 3.4rem;
		}
	}

	@include bp($point_4 - 1) {
		color: rgba(0, 0, 0, 0.8);
		letter-spacing: -0.016em;
		line-height: 1.42;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}
